<template>
  <div class="app-container">
    <header class="app-header">
      <div class="app-header-container">
        <div class="app-header-logo">
          <router-link to="/" class="app-header-logo-link">
            <img src="logo.png" class="app-header-logo-img" />
          </router-link>
        </div>

        <nav class="app-header-nav">
          <router-link class="app-header-link" to="/">
            <i class="zmdi zmdi-home"></i>
            {{ $t("message.app-home") }}
          </router-link>
          <!-- <router-link class="app-header-link" to="/collections">
            <img
              class="zmdi"
              src="../public/images/group.svg"
              alt=""
              srcset=""
            />
            {{ $t("message.app-collection") }}
          </router-link> -->
          <router-link class="app-header-link" to="/collections" :class="{'router-link-exact-active':'/collections'===$route.path}">
            <img
              class="zmdi"
              src="../public/images/group.svg"
              alt=""
              srcset=""
            />
            {{ $t("message.app-collection") }}
          </router-link>
          <router-link class="app-header-link" to="/search" :class="{'router-link-exact-active':'/search'===$route.path}">
            <i class="zmdi zmdi-search"></i>
            {{ $t("message.app-search") }}
          </router-link>
          <!--a class="app-header-link"
             @click="login()"
             v-if="isGuest">
            <i class="zmdi zmdi-account"></i>
            {{ $t("message.app-login") }}
          </a-->
          <a class="app-header-link" @click="logout()" v-if="!isGuest">
            <i class="zmdi zmdi-account"></i>
            {{ $t("message.app-logout") }}
          </a>
        </nav>
        <nav class="app-header-cart-nav">
          <!-- <router-link class="app-header-link cart-link" to="/cart"> -->
          <router-link class="cart-btn btn-primary" to="/cart">
            <i class="zmdi zmdi-shopping-cart"></i>
            <!-- <span style="font-size: 0.875rem;">search</span> -->
            <span style="">{{
              $t("message.cart-title")
            }}</span>
            <b-badge v-if="cartSize > 0">{{ cartSize }}</b-badge>
            <!-- <b-badge >100</b-badge> -->
          </router-link>
        </nav>
      </div>
    </header>
    <router-view></router-view>
    <footer class="app-footer">
     
      <div class="container d-flex " style="flex-direction: column;">
        <img src="../public/images/footer-ico.png" alt="">
        <p>
          {{ $t("message.app-copyright") }}
        </p>
       
      </div>
    </footer>
  </div>
</template>

<script>
import { constants } from "./constants.js";

export default {
  data() {
    return {
      isGuest: null,
    };
  },

  computed: {
    cartSize() {
      return this.$store.state.cartSize;
    },
  },
  created() {
    // Handle cart ID
    var cartId = this.$localStorage.get(
      constants.CART_ID_LABEL,
      constants.CART_NOT_FOUND
    );
    if (cartId === constants.CART_NOT_FOUND) {
      cartId = this.$uuid.v4();
      this.$localStorage.set(constants.CART_ID_LABEL, cartId);
    }
    this.$store.commit("setCartId", cartId);
    this.$store.dispatch("updateCart");
    this.testGuest();
    console.log('')
  },

  methods: {
    login() {
      var loginURL =
        "/nuxeo/logout?nxtimeout=true&forceAnonymousLogin=true&requestedUrl=catalog";
      window.location = loginURL;
    },

    logout() {
      var logoutURL =
        "/nuxeo/logout?nxtimeout=true&forceAnonymousLogin=false&requestedUrl=catalog";
      window.location = logoutURL;
    },

    // Note: this is used for filtering buttons. It is NOT related to security.
    testGuest() {
      this.$nuxeo
        .operation("login")
        .execute()
        .then(
          function(user) {
            if (user.username === "Guest") {
              this.isGuest = true;
            } else {
              this.isGuest = false;
            }
          }.bind(this)
        )
        .catch(function(error) {
          // something went wrong
          throw error;
        });
    },
  }, // End of methods
};
</script>
