    search() {
      this.queryParams.facetedKeyword_keywords_label = this.facetedKeyword_keywords_label;
      this.loading = true;
      this.searchFilters.isVisibleOnMobile = false;
      this.$nuxeo
        .request("search/pp/portal-search-pp/execute", {
          schemas: "*",
          headers: {
            "enrichers.document": "thumbnail,preview,permissions,codename",
            "X-NXfetch.aggregate": "key",
            "fetch.document": "properties",
            "X-NXtranslate.directoryEntry": "label",
          },
          queryParams: this.queryParams,
        })
        .get()
        .then(
          function(data) {
            this.results.splice(0, this.results.length);
            data.entries.forEach(
              function(entry) {
                this.results.push(entry);
              }.bind(this)
            );
            console.log(" this.results====", this.results)
            this.total = parseInt(data.totalSize);
            console.log(" this.total====", this.total)
            // this.facets = data.aggregations;
            // console.log("", this.facets);
            this.loading = false;
          }.bind(this)
        )
        .catch(function(error) {
          this.loading = false;
          this.$toasted.error(
            this.$t("message.search-error"),
            constants.TOASTED_DEFAULT_OPTIONS
          );
          throw error;
        });
    },
    refresh(e) {
      this.queryParams[e.facetName] = JSON.stringify(e.values);
      this.queryRoute[e.facetName] = JSON.stringify(e.values);
      this.$router.push({
        path: "/search",
        query: this.queryRoute,
      });
      this.search();
    },

    openGallery(index) {
