<template>
  <main class="main-content launch-collection">
    <Lightbox :images="images" ref="lightbox"></Lightbox>
    <div class="search">
      <aside
        class="search-filters"
        v-bind:class="[
          {
            sticky: searchFilters.isSticky,
            stickybottom: searchFilters.isStickyBottom,
            visible: searchFilters.isVisibleOnMobile,
          },
        ]"
      >
        <h2 class="collections-insights">
          {{ $t("message.collections-results-facets") }}
        </h2>

        <div class="search-filters-group">
          <!-- <h3 class="search-filters-group-title">
            {{ $t('message.search-filter-product-brand') }}
          </h3> -->
          <div class="search-filters-group-content">
            <TermCollection
              v-bind:data="collectionsList"
              v-on:selection-change="refresh"
            >
            </TermCollection>
          </div>
        </div>
      </aside>
      <div class="search-content">
        <!-- <div class="search-form">
          <input
            class="search-form-input"
            v-model="fulltext"
            :placeholder="$t('message.search-fulltext-placeholder')"
            v-on:keyup.13="_fulltext"
            v-on:input="_fulltext"
            style="height:41px;"
          />
          <b-button
            variant="primary"
            class="search-form-submit-btn"
            @click="_fulltext"
          >
            <i class="zmdi zmdi-search"></i>
            <span class="search-form-submit-btn-label">
              {{ $t("message.search-action-search") }}
            </span>
          </b-button>
          <button
            class="search-form-filters-btn"
            @click="toggleFiltersOnMobile()"
          >
            <i class="zmdi zmdi-filter-list"></i>
          </button>
        </div> -->

        <div class="search-results">
          <header class="search-results-header">
            <h2 class="search-results-header-title">
              {{ $t("message.search-result-count") }}:
              <strong>{{ total }}</strong>
            </h2>
            <div class="search-results-header-parameters pagesize">
              <label for="pagesize" style="width:250px;text-align:right;">
                {{ $t("message.search-results-per-page") }}
              </label>
              <multiselect
                v-model="pagesize"
                :options="sizes"
                track-by="value"
                label="label"
                :allow-empty="false"
                :showLabels="false"
                placeholder="Size"
              >
              </multiselect>
            </div>
          </header>
          <div class="search-results-content">
            <div class="search-results-loader" v-if="loading">
              <spinner :color="'#ef6d22'" :size="100" :depth="5"></spinner>
              <span class="search-results-loader-label">
                {{ $t("message.search-loading") }}
              </span>
            </div>
            <!--b-btn v-b-modal.modal1>Selection {{selects.length}}</b-btn-->

            <!-- Modal Component -->
            <b-modal id="modal1" title="Bootstrap-Vue" ref="selectionModal">
              <div
                v-for="(select, index) in selects"
                v-bind:key="select.uid"
                class="select"
              >
                <img
                  :src="select.contextParameters.thumbnail.url"
                  @click="openGallery(index)"
                  class="minithumb"
                />
                <div>{{ select.title }}</div>
                <b-button @click="unselect(select)">Unselect</b-button>
              </div>
              <b-button @click="addSelectionToCart()"
                >Add Selection to Cart</b-button
              >
              <b-button @click="clearSelects()">Clear Selection</b-button>
            </b-modal>
            <!--div v-if="selects.length>0">
              <b-button @click="addSelectionToCart()">Add Selection to Cart</b-button>
              <b-button @click="clearSelects()">Clear Selection</b-button>
            </div-->

            <b-row v-if="!loading">
              <b-col
                md="6"
                lg="4"
                xl="3"
                v-for="(result, index) in results"
                v-bind:key="result.uid"
              >
                <article class="product-item">
                  <div class="product-item-cover-wrapper">
                    <div
                      class="product-item-cover"
                      v-bind:style="{
                        backgroundImage:
                          'url(' + result.contextParameters.thumbnail.url + ')',
                      }"
                    ></div>
                    <div class="product-item-cover-actions">
                      <b-button
                        @click="openGallery(index)"
                        class="btn-primary preview-btn"
                      >
                        <i class="zmdi zmdi-zoom-in"></i>
                        {{ $t("message.search-action-details") }}
                      </b-button>
                      <div v-if="isInCart(result)">
                        <b-button @click="removeFromCart(result)">
                          {{ $t("message.search-action-remove-from-cart") }}
                        </b-button>
                      </div>
                      <div v-else>
                        <b-button
                          v-if="
                            result.contextParameters.permissions.indexOf(
                              'Read'
                            ) != -1
                          "
                          @click="addToCart(result)"
                          class="btn-primary cart-btn"
                        >
                          <i class="zmdi zmdi-shopping-cart-plus"></i>
                          {{ $t("message.search-action-add-to-cart") }}
                        </b-button>
                      </div>
                    </div>
                  </div>
                  <header
                    class="product-item-header"
                    @click="openGallery(index)"
                  >
                    <h4 class="product-item-title">
                      {{ result.title }}
                    </h4>
                  </header>
                  <!--div v-if="isSelect(result)">
                    <b-button @click="unselect(result)">Unselect</b-button>
                  </div>
                  <div v-else>
                    <b-button @click="select(result)">Select</b-button>
                  </div-->
                </article>
              </b-col>
            </b-row>
          </div>
          <footer class="search-results-footer">
            <b-pagination
              :total-rows="total"
              v-model="currentPage"
              :per-page="pagesize.value"
            >
            </b-pagination>
          </footer>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
import Multiselect from "vue-multiselect";
import "vue-multiselect/dist/vue-multiselect.min.css";
// import TermFacet from '../components/TermFacet.vue'
import TermCollection from "../components/TermCollection.vue";
import Lightbox from "../components/Lightbox.vue";
import Spinner from "vue-spinner-component/src/Spinner.vue";
import { constants } from "../constants.js";

export default {
  components: {
    Multiselect,
    TermCollection,
    Lightbox,
    Spinner,
  },

  data() {
    return {
      uid: "",
      collectionIds: [],
      fulltext: undefined,
      results: [],
      pagesize: { value: 24, label: "24" },
      currentPage: 1,
      total: 0,
      sizes: [
        { value: 12, label: "12" },
        { value: 24, label: "24" },
        { value: 48, label: "48" },
        { value: 96, label: "96" },
      ],
      facets: {},
      queryParams: {},
      selects: [],
      loading: false,
      searchFilters: {
        isSticky: false,
        isStickyBottom: false,
        offsetTop: 0,
        isVisibleOnMobile: false,
      },
      scrollTop: 0,
      collectionsList: [],
    };
  },
  computed: {
    images: function() {
      var images = [];
      this.results.forEach(function(item) {
        images.push({
          thumb: item.contextParameters.thumbnail.url,
          src: item.contextParameters.thumbnail.url,
          caption: item.title,
          document: item,
        });
      });
      return images;
    },
  },

  created() {
    this.fetchAllCollections();
    if (this.$route.query.uid) {
      this.uid = this.$route.query.uid.split(",");
      console.log("ccreated---", this.uid);

      // this.fetchDocumentsFromCollection(this.uid);
      this.fetchDocumentsInCollections(this.uid);
    }
    window.addEventListener("scroll", this.handleScroll);
  },

  mounted() {},

  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },

  methods: {
    refresh(e) {
      if (e.values) {
        this.collectionIds = e.values;
        this.$router.push({
          path: "/collections",
          query: {
            uid: e.uid,
          },
        });
        this.fetchDocumentsInCollections(this.collectionIds);
      }
    },
    _fulltext() {
      // this.queryParams.system_fulltext = this.fulltext;
      // this.search();
    },
    //launch collection
    fetchDocumentsFromCollection(uid) {
      this.loading = true;
      this.$nuxeo
        .operation("Collection.GetDocumentsFromCollection")
        .input(uid)
        .params({})
        .execute({
          schemas: "*",
          headers: {
            "fetch.document": "properties",
            properties: "product_search,picture,file",
            depth: "children",
            "enrichers.document": "thumbnail,permissions,preview,contextParameters",
          },
        })
        .then(
          function(data) {
            this.results.splice(0, this.results.length);
            data.entries.forEach(
              function(entry) {
                this.results.push(entry);
              }.bind(this)
            );
            this.total = parseInt(data.totalSize);
            this.loading = false;
          }.bind(this)
        )
        .catch(function(error) {
          this.loading = false;
          this.$toasted.error(
            this.$t("message.search-error"),
            constants.TOASTED_DEFAULT_OPTIONS
          );
          throw error;
        });
    },

    fetchDocumentsInCollections(collectionIds) {
      this.loading = true;
      this.$nuxeo
        .operation("GetDocumentsInCollections")
        .input(collectionIds)
        .params({
          currentPageIndex: 0,
          pageSize: 24,
          sortBy: "dc:title",
          sortOrder: "ASC",
        }) //{"currentPageIndex":0,"pageSize":24,"sortBy": "dc:title,product_search:brand","sortOrder":"ASC,ASC"}
        .execute({
          schemas: "*",
          headers: {
            "fetch.document": "properties",
            properties: "product_search,picture,file",
            depth: "children",
            "enrichers.document": "thumbnail,permissions,preview,contextParameters",
          },
        })
        .then(
          function(data) {
            this.results.splice(0, this.results.length);
            data.entries.forEach(
              function(entry) {
                this.results.push(entry);
              }.bind(this)
            );
            this.total = parseInt(data.totalSize);
            this.loading = false;
          }.bind(this)
        )
        .catch(function(error) {
          this.loading = false;
          this.$toasted.error(
            this.$t("message.search-error"),
            constants.TOASTED_DEFAULT_OPTIONS
          );
          throw error;
        });
    },

    fetchAllCollections() {
      this.$nuxeo
        .request("search/pp/campaign-splash-collection-search-pp/execute", {
          schemas: "*",
          headers: {
            "fetch.document": "properties",
            properties: "collection,picture,quicklink,file,campaigncollection",
            depth: "children",
          },
        })
        .get()
        .then(
          function(data) {
            this.collectionsList = data.entries;
            if (!this.uid && this.collectionsList.length) {
              this.getCollectionsUid();
            }
          }.bind(this)
        )
        .catch(function(error) {
          throw error;
        });
    },

    getCollectionsUid() {
      let collectionIds = [];
      this.collectionsList.forEach((item) => {
        if (item.properties["campaigncollection:collection"]) {
          collectionIds.push(
            item.properties["campaigncollection:collection"].uid
          );
        }
        if (item.properties["splashconfig:collection"]) {
          collectionIds.push(item.properties["splashconfig:collection"].uid);
        }
      });
      this.fetchDocumentsInCollections(collectionIds);
    },

    openGallery(index) {
      this.$refs.lightbox.showImage(index);
    },

    addToCart(result) {
      this.$nuxeo
        .operation("javascript.as_add_to_cart")
        .input(result.uid)
        .params({
          cartId: this.$store.state.cartId,
        })
        .execute()
        .then(
          function() {
            this.$toasted.success(
              this.$t("message.search-added-to-cart"),
              constants.TOASTED_DEFAULT_OPTIONS
            );
            this.$store.dispatch("updateCart");
          }.bind(this)
        )
        .catch(function(error) {
          this.$toasted.error(
            this.$t("message.search-unable-to-add"),
            constants.TOASTED_DEFAULT_OPTIONS
          );
          throw error;
        });
    },

    removeFromCart(result) {
      this.$nuxeo
        .operation("javascript.as_remove_from_cart")
        .input(result.uid)
        .params({
          cartId: this.$store.state.cartId,
        })
        .execute()
        .then(
          function() {
            this.$toasted.success(
              this.$t("message.search-removed-from-cart"),
              constants.TOASTED_DEFAULT_OPTIONS
            );
            this.$store.dispatch("updateCart");
          }.bind(this)
        )
        .catch(function(error) {
          this.$toasted.error(
            this.$t("message.search-unable-to-remove"),
            constants.TOASTED_DEFAULT_OPTIONS
          );
          throw error;
        });
    },

    select(result) {
      if (this.selects.indexOf(result) < 0) {
        this.selects.push(result);
      }
    },

    unselect(result) {
      var index = this.selects.indexOf(result);
      if (index >= 0) {
        this.selects.splice(index, 1);
      }
    },

    isSelect(result) {
      return this.selects.indexOf(result) >= 0;
    },

    addSelectionToCart() {
      this.$nuxeo
        .operation("Collection.AddToCollection")
        .input(this.selects)
        .param(
          "collection",
          "/default-domain/UserWorkspaces/Guest/Shopping_Cart"
        )
        .execute()
        .then(
          function() {
            this.$store.dispatch("updateCart");
            this.clearSelects();
            this.$refs.selectionModal.hide();
            this.$toasted.success(
              this.$t("message.Selection Added to Cart"),
              constants.TOASTED_DEFAULT_OPTIONS
            );
          }.bind(this)
        )
        .catch(function(error) {
          this.$toasted.error(
            this.$t("message.Could Not Add Selection to Cart"),
            constants.TOASTED_DEFAULT_OPTIONS
          );
          throw error;
        });
    },

    clearSelects() {
      this.selects.splice(0, this.selects.length);
    },

    isInCart(result) {
      var isInCart = this.$store.getters.isInCart(result.uid);
      return isInCart;
    },

    hasFacet(facetName) {
      return (
        this.facets[facetName] &&
        this.facets[facetName].buckets &&
        this.facets[facetName].buckets.length > 0
      );
    },

    //UI stuff

    calculateSearchFiltersPosition() {
      var $searchFilters = this.$el.querySelector(".search-filters");
      this.searchFilters.offsetTop = $searchFilters.getBoundingClientRect().top;
    },

    handleScroll() {
      this.scrollTop =
        window.pageYOffset !== undefined
          ? window.pageYOffset
          : (
              document.documentElement ||
              document.body.parentNode ||
              document.body
            ).scrollTop;

      if (this.scrollTop > this.searchFilters.offsetTop) {
        var appFooterOffsetTop = document
          .querySelector(".app-footer")
          .getBoundingClientRect().top;
        var searchFiltersHeight = this.$el.querySelector(".search-filters")
          .clientHeight;

        if (appFooterOffsetTop < searchFiltersHeight) {
          this.searchFilters.isStickyBottom = true;
          this.searchFilters.isSticky = false;
        } else {
          this.searchFilters.isSticky = true;
          this.searchFilters.isStickyBottom = false;
        }
      } else {
        this.searchFilters.isSticky = false;
        this.searchFilters.isStickyBottom = false;
      }
    },

    toggleFiltersOnMobile() {
      this.searchFilters.isVisibleOnMobile = !this.searchFilters
        .isVisibleOnMobile;
    },
  },
};
</script>

<style lang="scss">
.launch-collection {
  .collections-insights {
    font-size: 1.5rem;
    margin-bottom: 20px;
  }
}
</style>
