<template>
  <div>
    <div v-for="bucket in buckets" :key="bucket.key" class="search-termfacet">
      <input
        type="checkbox"
        :id="bucket.key"
        v-model="bucket.checked"
        v-on:change="_notifyChange"
      />
      <label :for="bucket.key">
        {{ bucket.label }} 
        <!-- <span class="search-termfacet-count">{{ bucket.docCount }}</span> -->
      </label>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    data: {
      type: Array,
      value: [],
    }
  },
  data() {
    return {
      buckets: [],
      uid:''
    };
  },
  watch: {
    data(data) {
      this._updateBuckets();
      return data;
    },
  },
  mounted() {
    this.$nextTick(()=>{
        this._updateBuckets();
    })
  },
  methods: {
    _notifyChange(e) {
      const values = [];
      let uid  = ''
      this.buckets.forEach((item) => {
        if (item.checked) {
          uid += `${item.key},`
          values.push(item.key);
        }
        //if(item.key !== e.target.id){
        //    item.checked = false
        //}
      });
      this.$emit("selection-change", {
        values: values,
        uid:uid.substr(0, uid.length -1)
      });
    },
    _updateBuckets() {
      
      if(!this.data.length) return 
      let uids = []
      if (this.$route.query.uid) {
        uids = this.$route.query.uid.split(',');
      }
      let buckets = [],uid = ''
      this.data.forEach((item) => {
        let checkStatus = false
        if(item.properties['campaigncollection:collection']){
            uid = item.properties['campaigncollection:collection'].uid;
        }
        else if(item.properties['splashconfig:collection']){
            uid = item.properties['splashconfig:collection'].uid;
        }
        if(uids.length){
          uids.forEach(i=>{
            if(item.properties['campaigncollection:collection'] && item.properties['campaigncollection:collection'].uid === i){
                checkStatus = true
            }
            else if(item.properties['splashconfig:collection'] && item.properties['splashconfig:collection'].uid === i){
                checkStatus = true
            }
            // if(item.properties['campaigncollection:collection'].uid === i) checkStatus = true
            return
          })
        }
        // if(uid == this.uid)  checkStatus = true
        item.label = item.title;
        buckets.push({
            checked:checkStatus,
            label:item.title,
            key:uid,
            // docCount:0
        })
      });
      buckets.sort((a, b) => {
        if (a.label < b.label) {
          return -1;
        } else if (a.label > b.label) {
          return 1;
        }
        return 0;
      });
      this.buckets = buckets;
    },
  },
};
</script>

<style scoped>
.search-filters {
    padding-bottom: 3rem;
}
</style>