<script>
export default {
  components: {},
  data() {
    return {
      assets: [],
      bannerImg: "",
      bannerUid:'',
      collectionsList: [],
      splashList: [],
      productTileList: [],
      isHover: false,
      hoverIndex: -1,
      hoverBrandIdx: -1,
    };
  },
  created() {
    this.fetchAllCollections();
    // this.fetchBrandLinks();
    this.fetchProductTiles();
    console.log("")
  },
  computed: {
    bannerPic() {
      return function(item) {
       let res;
       if(item.properties && item.properties['file:content']){
         res = item.properties['file:content'].data
       }else if(item.contextParameters && item.contextParameters.thumbnail){
         res = item.contextParameters.thumbnail.url
       }
       return res;
     };
    },
  },
  methods: {
    toCampaignCollections(){
      this.$router.push({
        path:'/collections',
        query:{
          uid:this.bannerUid
        }
      })
    },
    
    fetchAllCollections() {
      this.$nuxeo
        .request("search/pp/campaign-splash-collection-search-pp/execute", {
          schemas: "*",
          headers: {
            "fetch.document": "properties",
            properties: "collection,picture,quicklink,file,campaigncollection,splashconfig",
            depth: "children",
          },
          queryParams : {quicklink_active:"true"}
        })
        .get()
        .then(
          function(data) {
            let collectionsList = data.entries;
            let splashList = data.entries;
            //  this.collectionsList = collectionsList[0];
            for(var i=0; i< collectionsList.length; i++){
                if(collectionsList[i].type == "CampaignCollection" && this.collectionsList.length<6){
                    this.collectionsList.push(collectionsList[i]);
                }
                else if(collectionsList[i].type == "SplashConfig" && collectionsList[i].properties["file:content"]
                && collectionsList[i].properties['splashconfig:collection']
                && !this.bannerUid ) {
                    this.bannerUid = collectionsList[i].properties['splashconfig:collection'].uid
                    this.bannerImg = collectionsList[i].properties["file:content"].data;
                }
               
            }
            
            for(var i=0; i< splashList.length; i++){
                   if(splashList[i].type == "SplashConfig" && this.splashList.length < 1){
                    this.splashList.push(splashList[i]);
                }
            }

            this.$nextTick(()=>{
              if(this.collectionsList.length == 1){
                this.$refs.listPB[0].style.paddingBottom = '50%'
              }
            })
            
          }.bind(this)
        )
        .catch(function(error) {
          throw error;
        });
    },
    fetchProductTiles() {
      this.$nuxeo
        .request("search/pp/product-tile-search-pp/execute", {
          schemas: "*",
          headers: {
            "fetch.document": "properties",
            properties: "picture,quicklink,file,producttile",
            depth: "children",
            "enrichers.document": "thumbnail,permissions,documentURL",
          },
          queryParams : {quicklink_active:"true"}
        })
        .get()
        .then(
          function(data) {
            let entries = data.entries;
            let len
            if(entries.length > 6 && entries.length %6 ){
              len = 6 - entries.length % 6
               for (let index = 0; index < len; index++) {
                    entries.push({
                        uid:'',
                        properties:{},
                        contextParameters:{},
                        title:'',
                    })
               }
            }
            this.productTileList = entries
            this.$nextTick(()=>{
              if(this.productTileList.length == 1){
                this.$refs.brandsPB[0].style.paddingBottom = '50%'
              }
            })
          }.bind(this)
        )
        .catch(function(error) {
          throw error;
        });
    },
    handleHover(item) {
      console.log();
    },
    getAssetLink(asset) {
      return "/search?asset=" + asset.id;
    },
    getAsset(asset) {
      return asset.properties["dc:title"];
    },
  },
};
</script>

